/* ヘルパー */
/* ヘッダー */
.header_inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 10px 0;
}

.header_title {
  font-size: 2rem;
}

/* フッター */
.footer {
  position: sticky;
  top: 100vh;
  width: 100%;
  background-color: #eeeeee;
}

.footer_inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 10px 0;
}

.footer_text {
  font-size: 0.8rem;
}

/* コンテンツ */
.content {
  margin: 0 0 20px;
}

.content_inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #eeeeee;
  border-radius: 4px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.0862745098);
}

